/*
Variables found in globals.scss
$cloud-color;
$background-blue;

*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: $background-light;
  font-family: 'Lato', sans-serif;
  font-weight: 200;
  color: $font-dark;
  padding-left: 10%;
  padding-right: 10%;
}

@media (max-width: 400px) {
  body {
    padding-left: 0%;
    padding-right: 0%;
  }
}

h1 {
  font-weight: 300;
  font-size: 4em;
}

a h1 {
  display: inline-block;
}

h2 {
  font-weight: inherit;
  font-size: 2em;
}

h3 {
  font-weight: 500;
}

a {
  color: $font-dark;
}

article {
  margin: 6px;
  padding: 15px;
}

//// NEW

.cloud {
  background: $background-light-secondary;
  position: absolute;
  z-index: -1;
  height: 50px;
  width: 130px;
  margin-top: 70px;
  box-shadow: 10px 10px rgba(0, 0, 0, 0.2);
  border-radius: 100px;
}

.cloud::before,
.cloud::after {
  content: '';
  background: inherit;
  display: inline-block;
  position: relative;
  border-radius: inherit;
}

.cloud::before {
  width: 60px;
  height: 60px;
  top: -30px;
  left: 40px;
}
.cloud::after {
  width: 30px;
  height: 30px;
  top: -50px;
  left: -40px;
}

// Cloud specific positioning.
#cloud1 {
  top: 0px;
  left: 10%;
}

#cloud2 {
  top: 70px;
  left: 80%;
}

#cloud3 {
  top: 205px;
  left: 50%;
}

#cloud4 {
  top: -30px;
  right: 5%;
}

/* 
  * Header Section !
  */

div h1:nth-child(1) {
  letter-spacing: 0.021em;
}

img {
  border: solid;
  border-radius: 50%;
  width: 125px;
  margin-top: 45px;
}

#home-title {
  display: inline-block;
  float: left;
  padding-top: 25px;
  padding-bottom: 25px;
  height: 200px;
}

#home-image {
  display: inline-block;
  padding-left: 30px;
}
header {
  display: grid;
  justify-items: center;
}

/* 
  * Projects Section 
  */
#projects {
  display: grid;
  // grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  justify-items: center;
  padding: 6px;
  article {
    background-color: $background-light-secondary;
    position: relative;
    width: 500px;
    height: 250px;
    margin: 6px;
    padding: 30px;
    padding-bottom: 100px;
    border: solid;
    border-width: 3px;
    border-radius: 20px;
  }
}

.project-header {
  padding-bottom: 10px;
}

.project-content {
  display: grid;
  grid-template-columns: 50% auto;

  div {
    vertical-align: top;
    flex-grow: 1;
  }
  .project-about {
    max-height: 200px;
    max-width: 100%;
    overflow: scroll;
  }
}

.project-tags {
  height: 80px;
  padding-bottom: 10px;
  overflow: scroll;
  a {
    text-decoration: none;
    display: inline-block;
    background-color: #d1c4e9;
    padding: 5px;
    border-radius: 5px;
    margin-bottom: 3px;
    margin-right: 3px;
  }
}

.course-tag {
  position: absolute;
  right: 0px;
  top: 35px;
  text-align: center;
  background-color: #9cd0d0;
  transform: rotate(30deg);
}

.project-links {
  position: absolute;
  bottom: 0px;
  left: 0px;
  padding-top: 10px;
  display: grid;
  grid-template-columns: auto;
  width: 100%;
  padding: 0 30px 10px;
  a {
    text-decoration: none;
    margin: 3px;
    border: solid thin;
    font-size: 1.2em;
    border-radius: 5px;
    text-align: center;
  }
}
